export default {
  device() {
    const u = navigator.userAgent;
    // alert(u)
    const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
    const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
    if (isAndroid)
      return 'android';
    if (isiOS)
      return 'ios';
  }
};